.Menu {
    position: absolute;
    top: 2px;
    left: 2px;
    z-index: 1;

    padding: 10px;

    width: 230px;
    height: 100%;

    box-sizing: border-box;
    z-index: 2;
}

.Menu--Notes {
    height: 250px;
    overflow-y: auto;
    overflow-x: visible;
}

.Menu--Notes--Message {
  margin: 15px 5px !important;
}

.Menu--Archive-active {
    height: 200px;
    overflow-y: auto;
}

.Menu--Trigger {
    position: absolute;
    top: 12px;
    left: 12px;
    z-index: 2;
}
