html, body, #root, .Editor, .ProseMirror {
    width: 100%;
    height: 100%;
    caret-color: black;
}

.Editor {
    border: solid 1px black;
    overflow: auto;
    position: relative;
    background-color: #efefef;
    z-index: 1;
}

.Editor--menu button.active {
    background-color: blue;
}

.ProseMirror, .Editor--stats, .Editor--agenda, .Editor--help, .Editor--slides {
    position: relative;
    left: 100px;
    box-sizing: border-box;
    max-width: 800px;
    height: auto;
    margin: 10px auto 0;
    padding: 10px 10px 10px 50px;
    outline: none;
    background-color: white;

    border: 1px solid rgba(34,36,38,.15);
    box-shadow: 0 1px 2px 0 rgba(34,36,38,.15);
    border-radius: .28571429rem;
}

.Editor--menu {
    position: relative;
    left: 100px;
    box-sizing: border-box;
    max-width: 800px;
    height: auto;
    margin: 10px auto 0;
    outline: none;
}

.EditorWrapper {
    min-height: 100%;
}

:focus {
    outline: 0;
}

.ProseMirror p {
    position: relative;
}

.ProseMirror li p {
    display: inline;
}

.ProseMirror li, .ProseMirror p {
    margin: 0;
    padding: 0;
    line-height: 1.3;
}

.ProseMirror h1, .ProseMirror h2, .ProseMirror h3, .ProseMirror h4, .ProseMirror h5 {
    position: relative;
}

.Editor ul {
    margin: 0 !important;
}

/* -------------------------------------------------------------------------- */
/* Uploading */

.Editor--Uploading {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(238,238,238, 0.5);
    z-index: 1000;
}

/* -------------------------------------------------------------------------- */
/* Decorations */

.bold {
    font-weight: 700;
}

.italic {
    font-style: italic;
}

.underline {
    text-decoration: underline;
}

.striketrough {
    text-decoration: line-through;
}

.highlight {
    background-color: #FFFF00;
    color: #aaa;
}

.tag {
    color: #efefef;
    background-color: #B5CC17;
    cursor: pointer;
}

.hr {
    border-bottom: solid 1px;
    display: block;
    line-height: 1px;
    text-align: right;
}

.url {
    cursor: pointer !important;
    position: relative;
}

.url::before {
    content: 'Hold CMD and click to open';
    display: inline-block;
    width: 100%;
    padding-top: 5px;
    text-align: center;
    position: absolute;
    left: 0px;
    bottom: calc(100% - 18px);
    font-size: 8px;
    opacity: 0;
    transition: all 0.5s ease;
    cursor: pointer;
}

.url:hover::before {
    bottom: 100%;
    opacity: 1;
    background-color: #efefef;
}

.link--bracket {
    color: #aaa;
    font-style: italic;
}

.link--symbol {
    border: none;
    background: transparent;
    margin: 0;
    padding: 0;
    display: inline;
    text-align: center;
    outline: none;
}

.ProseMirror .img {
    max-width: 80%;
    max-height: 300px;
    clear: both;
}

.ProseMirror .img-text {
    display: inline-block;
    width: 0px;
    white-space: nowrap;
    overflow: hidden;
}

.ProseMirror .quote {
    color: white;
    border-left: 4px solid #B5CC17;
    padding: 1px;
}

.bullet {
    color: #B5CC17;
}

.ordered {
    color: #B5CC17;
}

.tableRow {
    color: green;
    font-family: monospace;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.ProseMirror .code {
    z-index: 2;
    color: transparent;
}

.code-highlight {
    top: 0;
    left: 0;
    position: absolute;
    z-index: -2;

    width: 100%;
    margin: 0;
    line-height: 1.3;

    color: blue;
    background-color: #f0f0f0;
    font-family: inherit;
}

.checkbox {
    font-family: monospace;
    overflow: hidden;
    white-space: nowrap;
    vertical-align: middle;
}

.ProseMirror .toggleFolded {
    cursor: pointer;
    border: solid 1px #B5CC17;
    color: #B5CC17;
    display: inline-block;
    padding: 0 5px;
    margin: 5px 0;
}
.ProseMirror .toggleFolded:hover {
  background-color: rgba(0, 0, 0, 0.02);
}

.ProseMirror .folded {
    display: none;;
}

.ProseMirror .folded.folded-opening {
    display: block;
    cursor: pointer;
    margin-left: -30px;
    padding: 0 5px;
    vertical-align: middle;
    color: #B5CC17;
    border: solid 1px #B5CC17;
}

.ProseMirror .folded.folded-opening:hover {
  background-color: rgba(0, 0, 0, 0.02);
}

/* TODO: */
.ProseMirror .heading + .folded-opening {}
.ProseMirror .heading + .folded.folded-opening::before {}


/* -------------------------------------------------------------------------- */
/* Date */

.date {
    color: #767676;
    text-decoration: underline #767676 dashed;
}

.date--button {
    font-size: 8px;
    display: inline-block;
    background: transparent;
    border: none;
    padding: 0 2px 0 0px;
    margin-left: 7px;
    cursor: pointer;
}

/* -------------------------------------------------------------------------- */
/* Duration */

.duration {
    color: #767676;
    text-decoration: underline #767676 dashed;
    cursor: pointer;
}

.duration--button {
    font-size: 8px;
    display: inline-block;
    background: transparent;
    border: none;
    padding: 0 2px 0 0px;
    margin-left: 7px;
    cursor: pointer;
}

.duration---overlay {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: 100;
}

.duration--menu {
    z-index: 2;
    width: 300px;
    padding: 5px;
    background-color: white;
    border: 1px solid rgba(34,36,38,.15);
    box-shadow: 0 1px 2px 0 rgba(34,36,38,.15);
    border-radius: .28571429rem;
}

/* -------------------------------------------------------------------------- */
/* Heading */

.ProseMirror .heading {
    margin: calc(2rem - .14285714em) 0 1rem;
    padding: 0 0;
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    font-weight: 700;
    line-height: 1.28571429em;
    text-transform: none;
    color: rgba(0,0,0,.87);
    margin-left: -30px;
}

.ProseMirror .heading-1 {
  min-height: 1rem;
  font-size: 2rem;
}
.ProseMirror .heading-2 {
  font-size: 1.71428571rem;
}
.ProseMirror .heading-3 {
  font-size: 1.28571429rem;
}
.ProseMirror .heading-4 {
  font-size: 1.07142857rem;
}
.ProseMirror .heading-5 {
  font-size: 1rem;
}

.heading--button {
    padding: 0;
    margin: 0;
    border: none;
    display: inline;
}


/* -------------------------------------------------------------------------- */
/* Counter */

.counter {
    border-bottom: 1px dotted #B5CC17;
}

.counter, .counter--button {
    font-family: monospace;
    text-align: center;
}

.counter--button {
    font-size: 8px;
    display: inline-block;
    background: transparent;
    border: none;
    cursor: pointer;
}

.counter--button:hover {
    color: #B5CC17;
}

/* -------------------------------------------------------------------------- */
/* Indicators */

.indicator {
    z-index: 2;
    position: absolute;
    right: calc(100% + 10px);
    top: calc(50% - 7.5px);
    font-size: 15px;
    cursor: pointer;
}

/* -------------------------------------------------------------------------- */
/* TODO */

.todo {

}

.todo-indicator.indicator {
    top: 0px;
    font-size: 10px;
    text-align: right;
    width: 200px;
    background-color: rgba(0, 0, 0, 0.05);
    padding: 10px;
    cursor: auto;
}

.todo-indicator.indicator .trigger {
    border: 1px solid transparent;
}

.todo-indicator.indicator .options {
    display: none;
}

.todo-indicator.indicator:hover .options {
    display: block;
    text-align: left;
    padding-top: 5px;
}

.todo-indicator.indicator button {
    cursor: pointer;
    border: 1px solid #B5CC17;
    margin: 2px;
}

/* -------------------------------------------------------------------------- */
/* TodoPopup */

.TodoPopup--item-count {
    float: right;
    vertical-align: top !important;
}

.TodoPopup--item-date {
    position: relative;
    top: 1px;
    left: -2px;
}

.TodoPopup--item-label {
    white-space: initial !important;
    max-width: 220px;
}
