.Agenda {

}

.Agenda--Row {
    overflow: hidden;
}

.Agenda--Row > * {
    vertical-align: middle !important;
}

.Agenda--TimeLabel {
    font-family: monospace !important;
    margin-right: 10px !important;
}

.Agenda--Checkbox {
    white-space: initial !important;
}

.Agenda--Checkbox--Label {
    margin-left: 5px;
    color: black;
}

.Agenda--Checkbox--Label.done {
    text-decoration: line-through;
}
